import React, { useState, useEffect } from "react";
import Navbar from './components/Navbar/nav'
import SearchContainer from './components/Search/search'
import Card from './components/Card/Card'
import SavedContainer from './components/Card/Card'
import axios from "axios";
import { ToastContainer, toast } from 'react-toastify';
import context from './components/Context/Context'
import deleteContext from './components/Context/Deletecontext'
import deleteBookContext from './components/Context/DeleteBookContext'
import 'bootstrap/dist/css/bootstrap.min.css';
import 'react-toastify/dist/ReactToastify.css';

function App() {
  
  const [findBook,setFindBook]=useState('')
  const [queryResult, setQueryResult]=useState([])
  const [saved, setSaved]=useState({})
  const [getCollection, setGetCollection]=useState([])
  

  function handleSearchChange(e){
  const {value}= e.target;
  setFindBook(value)
}


async function handleSubmit(){
  
  const title= await findBook.trim()
  const {data}= await axios.get(
    `https://www.googleapis.com/books/v1/volumes?q=${title}+intitle:${title}&key=${process.env.REACT_APP_KEY}`);
  if (data.totalItems > 0){
  setQueryResult(data.items)
  console.log(data.items)
  ;}


}
 
useEffect(() => {
  fetchData()
  async function fetchData(){
  
  const {data}=await axios.get(`https://www.googleapis.com/books/v1/volumes?q=react+intitle:react&key=${process.env.REACT_APP_KEY}`);
 await console.log({data})
 setQueryResult(data.items);
  }
  
},[]);


useEffect(() => {

  getBook()


},[saved]);

function savingBook(saveme){
axios.post("/api/book", saveme)
.then(res => toast.success('Book Saved'))
.catch(err => toast.info(err.response.data))
}
  
function deleteIt(id){
axios.delete("/api/book/"+id)
.then(res =>{ 
  console.log(res)
  toast.success('book is removed')})
.catch(err => console.log(err))

}

function deleteBook(id){
  axios.delete("/api/book/delete/"+id)
  .then(res => toast.success('Book deleted'))
  .catch(err => console.log(err))
  // console.log(id)
  }

  function getBook(){
    axios.get("/api/book")
  .then(res => {
  setGetCollection(res.data)
  })
  .catch(err => setGetCollection([]));
  }

  return (
    
    <div>
    <ToastContainer />
    <context.Provider value={getCollection}>
    <deleteContext.Provider value={deleteIt}>
    <deleteBookContext.Provider value={deleteBook}>
      <Navbar/>
      <div className="d-flex flex-column ">
      <div id="mainContainer" className="">
      <div id="searchContainer">
      <SearchContainer 
        handleSubmit={handleSubmit}
        handleSearchChange={handleSearchChange}
        findBook={findBook}
        queryResult={queryResult}
        
      />
      </div>
      <div className=" testimonial-group d-flex" id="container" >
      {queryResult.length>0 && queryResult.map((oneR,index) => (
        <Card 
        key={index}
          title={oneR.volumeInfo.title}
          author={oneR.volumeInfo.authors}
          description={oneR.volumeInfo.description}
          bookId={oneR.id}
          image={oneR.volumeInfo.imageLinks ? oneR.volumeInfo.imageLinks.thumbnail : `data:image/png;base64,`}
          publish={oneR.volumeInfo.publishedDate}
          link={oneR.volumeInfo.previewLink}
          setSaved={setSaved}
          saved={saved}
          savingBook={savingBook}
          getBook={getBook}
          setGetCollection={setGetCollection}


        />
      ))}
      </div>
      </div>
      <div>
        <SavedContainer getBook={getBook}/>
      </div>
      </div>
      </deleteBookContext.Provider>
      </deleteContext.Provider>
      </context.Provider>
    </div>
   
  );
}
//import React, { useState, useEffect } from "react";
export default App;
